// BODY STYLES
$slick-font-path: "./fonts/" !default;
$slick-font-family: "slick" !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: "\2190" !default;
$slick-next-character: "\2192" !default;
$slick-dot-character: "\25A0" !default;
$slick-dot-size: 18px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

@mixin aspect-ratio($width, $height) {
    position: relative;
    &:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }
    .content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

@function makelongshadow($color) {
    $val: 0px 0px $color;
    @for $i from 1 through 30 {
        $val: #{$val},
        #{$i}px #{$i}px #{$color};
    }
    @return $val;
}

@mixin longshadow($color) {
    text-shadow: makelongshadow($color);
}

@function makelongshadow_light($color) {
    $val: 0px 0px $color;
    @for $i from 1 through 15 {
        $val: #{$val},
        #{$i}px #{$i}px #{$color};
    }
    @return $val;
}

@mixin longshadow_light($color) {
    text-shadow: makelongshadow_light($color);
}

@function makelongshadow_title2($color) {
    $val: 0px 0px $color;
    @for $i from 1 through 20 {
        $val: #{$val},
        #{$i}px #{$i}px #{$color};
    }
    @return $val;
}

@mixin longshadow_light($color) {
    text-shadow: makelongshadow_title2($color);
}

