// SLIDER

.slider-section {
    margin-bottom: 36px;

    &__slider {
        width: 50%;
        @media (max-width: $screen-md-max) {
                width: 100%;
                float: none;    
        }

        height: 100%;
        @include aspect-ratio(925, 800);
        background: url("../img/slider_background.jpg") no-repeat center;
        float: left;
    }

    &__image{
        height: 100%;
        @include aspect-ratio(925, 800);
        float: left;
        width: 50%;
         @media (max-width: $screen-md-max) {
                width: 100%;
                float: none;    
            }
       }

    .content-slider_section{
        font-family: $FuturaHeavy;
        letter-spacing: 4px;
        font-size: 21px;
        line-height: 26px;
        
        &:before {
            content: "";
            display: inline-block;
            vertical-align: middle;
            height: 100%;
            width: 0px;
        }
    } 

    .slider{
        text-align: center;
        display: inline-block;
        width: 540px;
        margin: 0 auto; 
        vertical-align: middle;
        left: 50%;
        margin-left: -270px; 

        @media (max-width: $screen-lg-max) {
            width: 400px;
            margin-left: -200px; 
       }
    }   

    .slick-dotted.slick-slider {
        margin-bottom: 0px;
    }
    
    .slick-dots {
    position: absolute;
    bottom: -118px;
         @media (max-width: $screen-xs-max) {
            bottom: -59px;    
       }
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 20px;
                height: 20px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }
}


