.footer {
	background-image: -webkit-gradient(linear, left top, right bottom, color-stop(0, #f3f3f3), color-stop(100, #FFFFFF));
	padding: 43px 0;

	 &__links {
	 	margin-bottom: 10px;
	 	position: relative;

	
		&__icon{
			position: relative;
			margin-right: 12px; 
	 		float: left;
			width: 17px;
			height: 17px;
		}
	}
	
	&__text {
		font-size: 14px;
        font-family: $ActivGrotescLight;
        letter-spacing: 2px;
        font-weight: bold;
	}

	&__link-back {
		position: absolute;
		display: inline-block;
		width: 44px;
		height: 44px;
		background-color: #1a2631;
		right: 0px;
		&:before {
			content: "";
			position: absolute;
			width: 13px;
			height: 7px;
			background: url("../img/arrow__top.png");
			top: 17px;
			left: 15px;
		}	
	}

	.twitter {
		&:before {
			content: "";
			position: absolute;
			width: 17px;
			height: 12px;
			background: url("../img/sprite.png");
			background-position: -232px -112px;
			top: 1px;
			left: 2px;
		}	
	 }

	.facebook {
		&:before {
			content: "";
			position: absolute;
			width: 9px;
			height: 17px;
			background: url("../img/sprite.png") no-repeat center;
			background-position: -232px -148px;
			top: 0px;
			left: 2px;
		}
	}

	.be {
		&:before {
			content: "";
			position: absolute;
			width: 17px;
			height: 11px;
			background: url("../img/sprite.png") no-repeat center;
			background-position: -232px -137px;
			top: 3px;
			left: 0px;
		}
	}
	 
}