// $widthContent: 960px;
$widthPageMax: none;
$widthPageMin: 0;

html {
  height: 100%;
}
body {
  .no-flexbox & {
    min-height: 100%;
    height: 100%;
    display: block !important;
  }
}
.page-wrapper, .footer {
  max-width: $widthPageMax;
  min-width: $widthPageMin;
  width: 100%;
  margin: 0 auto;
  .no-flexbox & {
    width: auto;
  }
}
.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 35px 1.822916666666667%;
  background-color: #f3f3f3;
  .lte-ie-11 &, .lt-ie-11 & {
    min-height: 0;

  }
  .no-flexbox & {
    display: block !important;
    width: auto;
    min-height: 100%;
    height: auto !important;
    height: 100%;
  }
}
.page-body {
   flex: 1;
  &.flex-none {
    flex: none;
  }
}
.footer {
}
.wrapper {
  margin: 0 auto;
}

.page-section {
  margin-bottom: 36px;
}

.header_outer{
  text-align: center;
}

.vfix{height:100%;width:0px;display:inline-block;vertical-align:middle;}