@font-face {
	font-family: 'BaronNeue';
	src: url('fonts/BaronNeue/baron_neue-webfont.eot');
	src: url('fonts/BaronNeue/baron_neue-webfont.eot?#iefix') format('embedded-opentype'),
		 url('fonts/BaronNeue/baron_neue-webfont.woff2') format('woff2'),
		 url('fonts/BaronNeue/baron_neue-webfont.woff') format('woff'),
		 url('fonts/BaronNeue/baron_neue-webfont.ttf') format('truetype'),
		 url('fonts/BaronNeue/baron_neue-webfont.svg#BaronNeue') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'ActivGrotescLight';
	src: url('fonts/ActiveGroteskLight/aktiv_grotesk_light.eot');
	src: url('fonts/ActiveGroteskLight/aktiv_grotesk_light.eot?#iefix') format('embedded-opentype'),
		 url('fonts/ActiveGroteskLight/aktiv_grotesk_light.woff2') format('woff2'),
		 url('fonts/ActiveGroteskLight/aktiv_grotesk_light.woff') format('woff'),
		 url('fonts/ActiveGroteskLight/aktiv_grotesk_light.ttf') format('truetype'),
		 url('fonts/ActiveGroteskLight/aktiv_grotesk_light.ActivGrotescLight') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'FuturaHeavy';
	src: url('fonts/FuturaHeavy/futura_heavy.eot');
	src: url('fonts/FuturaHeavy/futura_heavy.eot?#iefix') format('embedded-opentype'),
		 url('fonts/FuturaHeavy/futura_heavy.woff') format('woff'),
		 url('fonts/FuturaHeavy/futura_heavy.ttf') format('truetype');
		font-weight: normal;
	font-style: normal;
}



$BaronNeue : 'BaronNeue', sans-serif;
$ActivGrotescLight : 'ActivGrotescLight', sans-serif; 
$FuturaHeavy : 'FuturaHeavy', sans-serif;