.header{
	
	.hamburger {
 		 top: 50px;
 		 left: 2px;
 		 display: block;
		 width: 30px;
		 height: 12px;
 		 position: relative;

 		 @media (max-width: $screen-xs-max) {
        	    top: 27px;
       	    }	

 		.line {
 			display: block;
 			width: 100%;
  			height: 2px;
 			background: #000;
    		position: absolute;
    		transition: all 200ms;
    		&:nth-child(1) {
      			top: 0;
    		}
    		&:nth-child(2) {
      			top: 50%;
      			transform: translateY(-50%);
    		}
    		&:nth-child(3) {
        		bottom: 0;
    		}
  		}

  		&.close {
    		.line {
      			&:nth-child(1),
      			&:nth-child(2) {
        			top: 0;
        			transform: translateY(5px) rotate(45deg);
      			}
      			&:nth-child(3) {
        			transform: translateY(-5px) rotate(-45deg);
      			}
    		}
 	 	}
  
  margin: auto; // centered for your amusement
	}

// extra crap

	body {
  		margin: 20px;
	}

	
	.icon-border{
		width: 77px;
		height: 111px;

		@media (max-width: $screen-xs-max) {
            width: 39px;
			height: 56px;
        }
	}

	.link-head {
		
		&__left {
		position: absolute;	
		border: 3px solid white;
		top: 58px;
		left: 61px; 

		@media (max-width: $screen-xs-max) {
            border: none;
            left: 12px;
        }


	
			&:before {
				content: ""; 
				position: absolute;
				background: url("../img/sprite.png");
				background-position: -232px 0px;
				width: 31px;
				height: 55px;
				top: 27px;
				left: 24px;

				@media (max-width: $screen-xs-max) {
           			top:0;
					left: 0;
      		  	}
			}

			&:after {
				content: "";
				position: absolute;
				box-sizing: border-box;
				width: 77px;
				height: 111px;
				border: 3px solid white;
				top: 3px;
				left: 3px;

				@media (max-width: $screen-xs-max) {
           			display: none;
      		  	}
			}
		}
		
		&__right {
			position: absolute;	
			background-image: -webkit-gradient(linear, left top, right bottom, color-stop(0, #e4e4e4), color-stop(100, #FFFFFF));
			top: 58px;
			right: 61px; 

			@media (max-width: $screen-xs-max) {
        	    top: 55px;
        	    right: 16px;
    	    }	
		
			&:before{
				content: "";
				position: absolute;
				box-sizing: border-box;
				width: 77px;
				height: 111px;
				top: 6px;
				left: 6px;
				background-image: -webkit-gradient(linear, left top, right bottom, color-stop(0, #e4e4e4), color-stop(100, #FFFFFF));
			
				@media (max-width: $screen-xs-max) {
           			width: 39px;
					height: 56px;
      		  	}
			}
		}	
	}
}

